<template>
    <h1 class="dashboard-title justify-content-between">
        <span class="top">Вывод средств</span>
        <div>
            <input type="text" name="search" class="form-input" v-model="searchValue" placeholder="Поиск" autocomplete="off">
        </div>
    </h1>
    <div class="dashboard-list">
        <div class="row grid-withdraw">
            <template v-for="item in filteredList" :key="item.currency">
                <div class="col-12 col-md-3 col-lg-7 col-xl-3" v-if="item.can_withdraw === true && search(item)">
                    <router-link 
                        :to="'/dashboard/withdraw/' + item.currency" 
                        class="list-item darks"
                    >
                        <img :src="item.logo" :alt="item.name">
                        <div class="list-item-wrap">
                            <div>
                                <b>{{ item.currency }}</b>
                                <span>{{ item.balance }}</span>
                            </div>
                            <small class="list-item-button d-none"></small>
                        </div>
                    </router-link>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            searchValue: ''
        }
    },
    computed: {
        list() {
            return this.$store.getters.getCurrencies || [];
        },
        filteredList() {
            return this.list.filter(item => item.can_withdraw === true && this.search(item));
        },
        isVerified() {
            return this.$store.getters.isVerified;
        }
    },
    methods: {
        search(item) {
            const searchValueLower = this.searchValue.toLowerCase();
            return item.currency.toLowerCase().includes(searchValueLower);
        },
    }
}
</script>
