import { createWebHashHistory, createRouter } from 'vue-router';
import AuthGuard from './auth-guard';
import Main from '@/pages/Main/Main';
import About from '@/pages/Main/About';
import News from '@/pages/Main/News';
import NewsDetail from '@/pages/Main/NewsDetail';
import FAQ from '@/pages/Main/FAQ';
import Contacts from '@/pages/Main/Contacts';
import Reviews from '@/pages/Main/Reviews';
import AddReview from '@/pages/Main/AddReview';
import DashboardMain from '@/pages/Account/DashboardMain';
import Dashboard from '@/pages/Account/Dashboard';
import Refill from '@/pages/Account/Refill';
import Buy from '@/pages/Account/Buy';
import BuyDetail from '@/pages/Account/BuyDetail';
import Sell from '@/pages/Account/Sell';
import SellDetail from '@/pages/Account/SellDetail';
import Withdraw from '@/pages/Account/Withdraw';
import WithdrawDetail from '@/pages/Account/WithdrawDetail';
import History from '@/pages/Account/History';
import Settings from '@/pages/Account/Settings';
import SettingProfile from '@/pages/Account/SettingProfile';
import SettingPassword from '@/pages/Account/SettingPassword';
import SettingVerification from '@/pages/Account/SettingVerification';
import SettingSecurity from '@/pages/Account/SettingSecurity';
import Security2fa from '@/pages/Account/Security2fa';
import Registration from '@/pages/Auth/Registration';
import Login from '@/pages/Auth/Login';
import Restore from '@/pages/Auth/Restore';
import Payment from '@/pages/Account/Payment.vue';
import PaymentServices from '@/pages/Account/PaymentServices.vue';
import PaymentDetail from '@/pages/Account/PaymentDetail.vue';

const routes = [
  {
    name: 'Main',
    path: '/',
    component: Main,
  },
  {
    name: 'About',
    path: '/about',
    component: About,
  },
  {
    name: 'News',
    path: '/news',
    component: News,
  },
  {
    name: 'News Detail',
    path: '/news/:id',
    component: NewsDetail,
  },
  {
    name: 'FAQ',
    path: '/faq',
    component: FAQ,
  },
  {
    name: 'Contacts',
    path: '/contacts',
    component: Contacts,
  },
  {
    name: 'Reviews',
    path: '/reviews',
    component: Reviews,
  },
  {
    name: 'Add review',
    path: '/reviews/add',
    component: AddReview,
    beforeEnter: AuthGuard,
  },
  {
    name: 'registration',
    path: '/registration',
    component: Registration,
  },
  {
    name: 'login',
    path: '/login',
    component: Login,
  },
  {
    name: 'restore',
    path: '/restore',
    component: Restore,
  },
  {
    name: 'Dashboard Main',
    path: '/dashboard',
    component: DashboardMain,
    beforeEnter: AuthGuard,
    children: [
      {
        name: 'Dashboard',
        path: '',
        component: Dashboard,
      },
      {
        name: 'Refill',
        path: 'refill',
        component: Refill,
      },
      {
        name: 'Refill Category',
        path: 'refill/:category',
        component: Refill,
      },
      {
        name: 'Buy',
        path: 'buy',
        component: Buy,
      },
      {
        name: 'Buy Detail',
        path: 'buy/:currency',
        component: BuyDetail,
      },
      {
        name: 'Sell',
        path: 'sell',
        component: Sell,
      },
      {
        name: 'Sell Detail',
        path: 'sell/:currency',
        component: SellDetail,
      },
      {
        name: 'Withdraw',
        path: 'withdraw',
        component: Withdraw,
      },
      {
        name: 'Withdraw Detail',
        path: 'withdraw/:currency',
        component: WithdrawDetail,
      },
      {
        name: 'History',
        path: 'operations',
        component: History,
      },
      {
        name:'Payment ',
        path:'payment-services',
        component:Payment,
      },
      {
        name:'Payment Services',
        path:'payment-service/:id',
        component:PaymentServices,
      },
      {
        name:'Payment Detail',
        path:'payment-detail/:id',
        component:PaymentDetail,
      },
      {
        name: 'Settings',
        path: 'settings',
        component: Settings,
        children: [
          {
            name: 'Profile',
            path: '',
            component: SettingProfile,
          },
          {
            name: 'Change Password',
            path: 'password',
            component: SettingPassword,
          },
          {
            name: 'Verification',
            path: 'verification',
            component: SettingVerification,
          },
          {
            name: 'Security',
            path: 'security',
            component: SettingSecurity,
          },
          {
            name: 'Security 2Fa',
            path: 'security/2fa',
            component: Security2fa,
          },
        ],
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior() {
    document.getElementById('app').scrollIntoView({ behavior: 'smooth' });
  },
});

export default router;
