<template>
    <div>
        <h1 class="dashboard-title top-dashboard">Мои кошельки</h1>
        <div class="flex-balance">
            <div v-if="balances && !loading" class="balans">
                <div v-for="balance in balances" :key="balance" class="balance-wrap">
                   <div class="currencies-column ">
                    <img :src="balance.logo" :alt="balance.name">
                   <div class="balance-text">
                    <b>{{ balance.currency }}</b>
                    <b class="pri">{{ balance.name }}</b>
                   </div>
                   </div>
                    <div class="balance-text" style="text-align:center">
                    <span>{{ balance.balance }}</span>
                    <span v-if="balance.rate" :class="String(balance.difference).search('-') !== -1 ? 'text-danger grin' : 'text-success grin'">${{ balance.rate }}</span>
                    </div>
                    <div class="balance-footer">
                        <router-link :to="'/dashboard/refill/cryptocurrency?currency=' + balance.id" class="button-t md block" v-if="balance.can_deposit === true">Ввод</router-link>
                        <a class="button-t md block disabled" v-else>Ввод</a>
                        <router-link :to="'/dashboard/withdraw/' + balance.currency" class="button-m md block" v-if="balance.can_withdraw === true">Вывод</router-link>
                        <a class="button-m md block disabled" v-else>Вывод</a>
                      </div>
                 
                </div>
                
              </div>
        
                <div class="col-12 col-md-12 col-lg-12 ">
                    <div class="row">
                     
                        <div class="col-lg-6 col-xl-12">
                            <div class="dashboard-personal-wrap" v-if="profile">
                                <h1 class="title">Лимиты за текущий день</h1>
                              <div class="limit-balance">
                                <div class="profile-info-column-date refill">
                                    <div>
                                        <span>Пополнение</span>
                                        <b>{{ profile.limits.refill }} USDT</b>
                                    </div>
                                </div>
                                <div class="profile-info-column-date buy">
                                    <div>
                                        <span>Покупка</span>
                                        <b>{{ profile.limits.buy }} USDT</b>
                                    </div>
                                </div>
                                <div class="profile-info-column-date sell">
                                    <div>
                                        <span>Продажа</span>
                                        <b>{{ profile.limits.sell }} USDT</b>
                                    </div>
                                </div>
                              </div>
                            </div>
                        </div>
                        <div class="col-lg-6 col-xl-12">
                            <div class="dashboard-personal-wrap" v-if="profile">
                                <h1 class="title">Персональные данные</h1>
                                <div class="profile-info-column calendar">
                                    <div>
                                        <span>Дата регистрации</span>
                                        <b>{{ profile.date_register }}</b>
                                    </div>
                                </div>
                                <div class="profile-info-column exit">
                                    <div>
                                        <span>Последний вход</span>
                                        <b>{{ profile.last_access }}</b>
                                    </div>
                                </div>
                                <div class="profile-info-column email">
                                    <div>
                                        <span>Электронная почта</span>
                                        <b>{{ profile.email }}</b>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    
          
         
    </div>
</template>

<script>

export default {
    data() {
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        balances() {
            return this.$store.getters.getCurrencies
        },
        profile() {
            return this.$store.getters.getProfile
        },
        actions() {
            return (this.$store.getters.getActionList)
            ? this.$store.getters.getActionList
            : this.$store.dispatch('getActionList')
        }
    },
}
</script>