<template>
    <div id="slider">
      <Splide :options="options">
        <SplideSlide v-for="(image, index) in images" :key="index" class="sliders">
          <div class="mn">
            <img :src="image.src" :alt="image.alt">
          </div>
        </SplideSlide>
      </Splide>
    </div>
  </template>
  
  <script>
  import { Splide, SplideSlide } from '@splidejs/vue-splide'
  import '@splidejs/vue-splide/css'
  
  export default {
    components: {
      Splide,
      SplideSlide,
    },
    data() {
      return {
        images: [
          { src: require('@/assets/img/slider1.svg'), alt: 'Slider 1' },
          { src: require('@/assets/img/bitfinex1.svg'), alt: 'Bitfinex 1' },
          { src: require('@/assets/img/crakenm.svg'), alt: 'Crakenm' },
          { src: require('@/assets/img/binanse.svg'), alt: 'Crakenm' },
          { src: require('@/assets/img/buybt.svg'), alt: 'Buy BT' },
        ],
        options: {
          type: 'loop',
          perPage: 1,
          gap: '20px',
          arrows: false,
          pagination: false,
          autoplay: true,
          interval: 2500,
          speed: 300,
          pauseOnHover: false,
          breakpoints: {
            1366: {
              perPage: 5,
            },
            1200: {
              perPage: 4,
            },
            992: {
              perPage: 4,
            },
            768: {
              perPage: 3,
            },
            576: {
              perPage: 2,
            },
          },
        }
      }
    }
  }
  </script>
  
  <style>

  #slider {
    width: 100%;
    margin: 0 auto;
  }
  
  .mn img {
    width: 200px;
    height: 40px;
    margin: auto;
    display: flex;
    gap: 30px;
    
  }</style>