<template>
  <div class="left-bar" :class="status && 'active'">
    <div class="profile-info">
      <div class="name-photo">
        <div class="photo-wrap">
          <img :src="profile.avatar + '?v=1.0.0'" :alt="profile.email" />
        </div>
        <div class="name-fio">
          <b>{{ profile.firstname + " " + profile.lastname }}</b>
          <router-link to="/dashboard/settings/verification"
            v-if="profile?.verification?.value == 1"
            class="text-dangers log"
          >
            {{ profile.verification.name }}
          </router-link>
          <router-link to="/dashboard/settings"
            @click="goToVerificationPage"
            v-if="profile?.verification?.value == 2"
            class="text-successs log"
          >
            {{ profile.verification.name }}
          </router-link>
          <span
            v-if="profile?.verification?.value == 3"
            class="text-warning log"
          >
            {{ profile.verification.name }}
          </span>
          <span
            v-if="profile?.verification?.value == 4"
            class="text-danger log"
          >
            {{ profile.verification.name }}
          </span>
        </div>
      </div>
      <div class="left-bar-balance">
        <h6>Баланс:</h6>
        <b>
          <h6 class="balance-left">{{ firstBalance.balance}}</h6>
          <span>USDT</span>
          <img :src="firstBalance.logo" class="logoleft-bar" alt="" />
        </b>
      </div>
    </div>
    <div class="dashboard-menu">
      <template v-for="item in menu" :key="item.name">
        <router-link
          :to="item.link"
          class="dashboard-nav ease"
          :class="[
            item.icon,
            item.name === 'Вывод на карты' ? 'card-menu-item' : '',
            item.class || '',
          ]"
          v-if="!item.disabled"
          >{{ item.name }}</router-link
        >
        <a class="dashboard-nav ease disabled" v-else>{{ item.name }}</a>
      </template>
      <a href="#" class="dashboard-nav ease logout" v-on:click="logout"
        >Выйти</a
      >
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menu: [
        {
          name: "Вывод на карты",
          icon: "payment",
          link: "/dashboard/payment-service/259",
        },
        { name: "Главная", icon: "home", link: "/dashboard" },
        {
          name: "Вывод на карты",
          icon: "payments",
          link: "/dashboard/payment-service/259",
          class: "hide-on-small-screens",
        },
        {
          name: "Пополнить",
          icon: "refill",
          link: "/dashboard/refill/cryptocurrency",
        },
        { name: "Вывести", icon: "withdraw", link: "/dashboard/withdraw" },
        {
          name: "Оплата услуг",
          icon: "pay",
          link: "/dashboard/payment-services",
        },
        {
          name: "Купить",
          icon: "buy",
          link: "/dashboard/buy",
          label: "Торговля",
        },
        { name: "Продать", icon: "sell", link: "/dashboard/sell" },
        {
          name: "История операции",
          icon: "history",
          link: "/dashboard/operations",
        },
        {
          name: "Настройки",
          icon: "settings",
          link: "/dashboard/settings",
          label: "Настройки",
        },
      ],
    };
  },
  props: {
    status: Boolean,
  },
  computed: {
  profile() {
    return (
      this.$store.getters.getProfile || this.$store.dispatch("getProfile")
    );
  },
  balance() {
    return this.$store.getters.getCurrencies;
  },
  firstBalance() {
    const balances = this.$store.getters.getCurrencies;
    if (balances && balances.length > 0) {
      const balance = balances.find(b => b.id === '1');
      return balance ? balance : { balance: 0, logo: '', currency: 'USDT' };
    } else {
      return { balance: 0, logo: '', currency: 'USDT' };
    }
  },
},
  methods: {
    goToVerificationPage() {
      if (this.profile?.verification?.value === 2) {
        this.$router.push("/dashboard/settings/verification");
      }
    },
    logout() {
      this.$store.dispatch("logout").then(() => {
        this.$router.push("/login");
      });
    },
  },
};
</script>