<template>
  <h1 class="dashboard-title justify-content-between">
    <span class="top">Продать криптовалюту</span>
    <div>
      <input
        type="text"
        name="search"
        class="form-input-buy"
        v-model="searchValue"
        placeholder="Поиск"
        autocomplete="off"
      />
    </div>
  </h1>
  <div class="currencies-table">
    <template v-for="currency in currencies" :key="currency">
      <div
        class="currencies-row border-bottom buy-padding"
        v-if="currency.can_sell === true && search(currency)"
      >
        <div class=" currencies-column currencies-column-md m">
          <img :src="currency.logo" :alt="currency.name" class="none"/>
          <div class="currency-name">
            <span>{{ currency.currency }}</span>
            <b>{{ currency.name }}</b>
          </div>
        </div>
        <div class=" currencies-column currencies-column-md m">
          <div style="text-align: center">
            ${{ currency.rate }}
            <span class="currencies-column pri">Цена</span>
          </div>
        </div>
        <div class="  currencies-column currencies-column-md m">
          <div class="">
            {{ currency.balance }}
            <span
              class="currencies-column"
              v-if="currency.converted_balance"
              :class="
                String(currency.converted_balance).search('-') !== -1
                  ? 'text-danger'
                  : 'text-success'
              "
              >{{ currency.converted_balance }}</span
            >
          </div>
        </div>
        <div class="currencies-column currencies-column-md">
          <router-link
            :to="'/dashboard/sell/' + currency.currency"
            class="button button-danger"
            :class="{ 'disabled-button': currency.balance <= 0 }"
            @click="handleBuy(currency)"
            >Продать</router-link
          >
        </div>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchValue: "",
      chartOptions: {
        chart: {
          toolbar: {
            show: false,
          },
          sparkline: {
            enabled: true,
          },
          parentHeightOffset: 0,
        },
        fill: {
          type: "gradient",
          gradient: {
            shadeIntensity: 1,
            opacityFrom: 0.7,
            opacityTo: 0.9,
            colorStops: [
              {
                offset: 60,
                color: "#46E3AC",
                opacity: 0.1,
              },
              {
                offset: 70,
                color: "#46E3AC",
                opacity: 0.08,
              },
              {
                offset: 100,
                color: "transparent",
                opacity: 1,
              },
            ],
          },
        },
        dataLabels: {
          enabled: false,
        },
        xaxis: {
          show: false,
          labels: {
            show: false,
          },
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
        },
        yaxis: {
          labels: {
            show: false,
          },
        },
        grid: {
          show: false,
        },
        stroke: {
          width: 1,
          colors: ["#46E3AC"],
          curve: "smooth",
        },
        tooltip: {
          enabled: false,
        },
      },
    };
  },
  computed: {
    currencies() {
      return this.$store.getters.getCurrencies;
    },
  },
  methods: {
    rateFormat(data) {
      const rates = [];
      Object(data.rates).forEach((item, index) => {
        rates[index++] = item.price;
      });
      return [{ data: rates }];
    },
    search(item) {
      return item.currency.search(this.searchValue) !== -1 ||
        item.currency.toLowerCase().search(this.searchValue) !== -1
        ? true
        : false;
    },
    handleBuy(currency){
      if(currency.balance>0){
        this.$store.push(`/dashboard/buy${currency.id}`)
      }else{
        this.$store.commit('setModalCurrency',currency.name);
        this.$store.commit('toggleModal',true);
        this.$router.push(`/dashboard/refill/cryptocurrency?currency=${currency.id}`)
      }
    }
  },
};
</script>