<template>
  <section class="slider">
    <div class="container slider-main">
      <div class="row  slider-item">
        <div class="col-md-6 col-sm-12 slider-main-text">
          <img class="xbt-logo-img" :src="xbt" />
          <span>- Покупка и продажа криптовалюты не выходя из дома</span>
          <p class="about-text">
            Прозрачные комиссии, наличие лицензии, большие резервы и отличная
            репутация гарантируют успех ваших обменных операций!
          </p>
          <div class="qr-code none">
           <div class="qr-coder">
            <img :src="qrcode"/>
           </div>
            <div class="qr-code-text">
              <p class="apdds">Отсканируйте, чтобы загрузить приложение</p>
              <p class="ios-android" @click="openAndroidStore">iOS и Android</p>
            </div>
          </div>
        </div>
        <div class="relative">
          <div class="banner">
  
          </div>
         </div>
            <div class="qr-codes top-up-item">
             <div class="qr-coder">
              <img :src="qrcode" />
             </div>
              <div class="qr-code-text">
                <p class="apdds">Отсканируйте, чтобы загрузить приложение</p>
                <p class="ios-android" @click="openAndroidStore">iOS и Android</p>
              </div>
            </div>
      </div>
    </div>
  </section>
</template>

<script>
import USDIcon from "../../assets/img/icons/usd.png";
import KGSIcon from "../../assets/img/icons/flag-kg.png";

export default {
  data() {
    return {
      imageUrl: require("../../assets/img/banner.svg"),
      qrcode: require("../../assets/img/qr-mobile.svg"),
      xbt: require("../../assets/img/Xbt.kg.svg"),
      modalStatus: false,
      type: "buy",
      sendAmount: 92000,
      sendCurrency: "",
      receiveAmount: 1000,
      receiveCurrency: "",
      currencyList: [
        {
          logo: USDIcon,
          name: "US Dollar",
          currency: "USD",
        },
        {
          logo: KGSIcon,
          name: "СОМ",
          currency: "KGS",
        },
      ],
    };
  },
  mounted() {
    if (this.currencyList[0]) {
      this.sendCurrency = this.currencyList[0];
    }
  },
  computed: {
    currencies() {
      return this.$store.getters.getCurrencies
        ? this.$store.getters.getCurrencies
        : this.$store.dispatch("getCurrencies");
    },
  },
  watch: {
    currencies() {
      this.receiveCurrency = this.currencies.USDT;
    },
    type() {
      if (this.type == "buy") {
        this.sendCurrency = this.currencyList[0];
        this.receiveCurrency = this.currencies.USDT;
      }
      if (this.type == "sell") {
        this.sendCurrency = this.currencies.USDT;
        this.receiveCurrency = this.currencyList[0];
      }
    },
  },
  methods: {
    openAndroidStore() {
      window.location.href = "https://play.google.com/store/apps/details?id=com.navisdevs.xbt"; 
    },
    modal(status) {
      this.modalStatus = status;
    },
    selectSendCurrency(item) {
      this.modalStatus = false;
      this.sendCurrency = item;
    },
    selectReceiveCurrency(item) {
      this.modalStatus = false;
      this.receiveCurrency = item;
    },
    calculate() {
      const result =
        this.sendAmount / 89.77 / this.receiveCurrency.rates[0].price;
      this.receiveAmount = result.toFixed(this.receiveCurrency.decimal);
    },
  },
};
</script>
