<template>
    <div class="header">
      <div class="header-menu">
        <div class="container" :class="menuStatus && 'z-index'">
          <router-link to="/dashboard" class="logos nam"></router-link>
  
          <router-link to="/" class="logo"></router-link>
          <div class="mobile-menu" v-on:click="showMenu" :class="menuStatus && 'open'">
            <span></span>
            <span></span>
            <span></span>
          </div>
          <div class="header-nav" :class="menuStatus && 'active'">
            <div class="logo-menu">
              <div class="mobile-menu" v-on:click="showMenu" :class="menuStatus && 'open'">
                <span></span>
                <span></span>
                <span></span>
              </div>
              <router-link to="/" class="logo"></router-link>
              <div></div>
            </div>
            <router-link to="/" class="menu" active-class="router-link-active">
              <b>Главная</b>
              <span>Главная</span>
            </router-link>
            <router-link to="/about" class="menu" active-class="router-link-active">
              <b>О компании</b>
              <span>О компании</span>
            </router-link>
            <router-link to="/news" class="menu" active-class="router-link-active">
              <b>Новости</b>
              <span>Будь в курсе</span>
            </router-link>
            <router-link to="/faq" class="menu" active-class="router-link-active">
              <b>FAQ</b>
              <span>Вопрос/Ответы</span>
            </router-link>
            <router-link to="/reviews" class="menu" active-class="router-link-active">
              <b>Отзывы</b>
              <span>Что о нас говорят?</span>
            </router-link>
            <router-link to="/contacts" class="menu" active-class="router-link-active">
              <b>Контакты</b>
              <span>Свяжитесь с нами</span>
            </router-link>
            <template v-if="String(this.$router.currentRoute.value.path).search('dashboard') !== -1">
              <router-link to="/dashboard" class="auth-menu-dash active" v-if="isAuth">Личный кабинет</router-link>
            </template>
            <template v-else>
              <a href="#" class="auth-menu ml-auto" v-if="isAuth" v-on:click="logout">Выйти</a>
              <router-link to="/login" class="auth-menu ml-auto" v-else>Войти</router-link>
              <router-link to="/dashboard" class="auth-menu active" v-if="isAuth">Личный кабинет</router-link>
              <router-link to="/registration" class="auth-menu active" v-else>Регистрация</router-link>
            </template>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        isAuth: false,
        menuStatus: false
      }
    },
    created() {
      if (localStorage.getItem('isAuth') === 'true' && localStorage.getItem('token')) {
        this.isAuth = true
      }
    },
    computed: {
      balance() {
        return (this.$store.getters.getCurrencies)
          ? this.$store.getters.getCurrencies
          : this.$store.dispatch('getCurrencies')
      }
    },
    methods: {
      showMenu() {
        this.menuStatus = (this.menuStatus) ? false : true
      },
      logout() {
        this.$store.dispatch('logout')
        this.isAuth = false
      }
    }
  }
  </script>
  