<template>
  <div>
    <h1 class="dashboard-title justify-content-between">
      <span class="top">Оплата услуг</span>
      <div>
        <input
          type="text"
          name="search"
          class="form-input-buy"
          v-model="searchValue"
          placeholder="Поиск"
          autocomplete="off"
        />
      </div>
    </h1>
  <div class="list-payment">
        <router-link 
          v-for="detail in filteredPaymentDetail" 
          :key="detail.id"  
          :to="'/dashboard/payment-service/' + detail.id"
          class="detail-payment"
        >
          <img :src="detail.logo" :alt="detail.name" />
          <span>{{ detail.name }}</span>
        </router-link>
      </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      searchValue: '',
    };
  },
  computed: {
    paymentDetail() {
      return this.$store.getters.paymentDetail;
    },
    filteredPaymentDetail() {
      if (!this.searchValue) {
        return this.paymentDetail; 
      }
      const searchTerm = this.searchValue.toLowerCase();
      return this.paymentDetail.filter(detail => {
        return detail.name.toLowerCase().includes(searchTerm);
      });
    },
  },
  mounted() {
    this.$store.dispatch('getPaymentDetail', '');
  },
};
</script>
