<template>
    <Header></Header>
    <div class="container min-height padding-top-default">
      <div class="logoat">
        <router-link to="/" class="home-logoaut">Главная</router-link>
        <img src="./../../assets/img/icons/left.svg" alt="" />
        <span class="newws">Новости</span>
      </div>
  
      <div class="news-page z-index">
        <h1 class="title">Последние новости</h1>
        <div class="news-detial-all">
          <div class="gris" v-if="NewsList && NewsList.news">
            <div
              class="news-wrap"
              v-for="(news, index) in NewsList.news"
              :key="index"
            >
              <router-link :to="'/news/' + news.id">
                <div class="news-thumbnail">
                  <img :src="news.photo" :alt="news.name" />
                </div>
              </router-link>

                <p class="news-description">{{ news.body }}</p>
                <p class="news-date">{{ news.date }}</p>
            </div>
          </div>
          <div v-else>
            <p>Loading news...</p>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </template>
  

<script>
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";

export default {
  components: {
    Header,
    Footer,
  },
  computed: {
    NewsList() {
      return this.$store.getters.getNewsList
        ? this.$store.getters.getNewsList
        : this.$store.dispatch("getNewsList");
    },
  },
};
</script>
