<template>
  <div class="row dashboard-title justify-content-between">
    <div class="dashboard-personal-wrap payment-detail">
      <form v-on:submit.prevent="onsubmit" class="form" autocomplete="off">
        <label class="form-label d-flex justify-content-between">
          Сумма к списанию
          <span>
            <span class="md-none"></span>
            <b>{{ firstBalance.balance }}</b> USDT
          </span>
        </label>
        <div class="form-group position mb-4">
          <input
            type="number"
            name="amount"
            class="form-input"
            step="any"
            placeholder="Введите сумму"
            v-model="amount"
            @input="calculate"
            required
            :disabled="loading"
          />
          <b class="absolute" @click="setMaxAmount">MAX</b>
        </div>
        <label class="form-label d-flex justify-content-between">Сумма к зачислению</label>
        <div class="form-group">
          <input
            type="number"
            name="credit"
            class="form-input"
            placeholder="Сумма к зачислению"
            v-model="credit"
            disabled
          />
        </div>
        <label class="form-label d-flex justify-content-between">
          {{ paymentDetails && paymentDetails.description ? paymentDetails.description : "Введите реквизит" }}
        </label>
        <div class="form-group position mb-4">
          <input
            type="text"
            name="address"
            class="form-input"
            v-bind:placeholder="paymentDetails && paymentDetails.description ? paymentDetails.description : 'Введите реквизит'"
            v-model="address"
            required
          />
          <svg
            xmlns="http://www.w3.org/2000/svg"
            @click="pasteFromClipboard"
            height="24"
            width="21"
            viewBox="0 0 448 512"
            class="absoluteIcon width-s"
          >
            <path
              fill="#B197FC"
              d="M384 336H192c-8.8 0-16-7.2-16-16V64c0-8.8 7.2-16 16-16l140.1 0L400 115.9V320c0 8.8-7.2 16-16 16zM192 384H384c35.3 0 64-28.7 64-64V115.9c0-12.7-5.1-24.9-14.1-33.9L366.1 14.1c-9-9-21.2-14.1-33.9-14.1H192c-35.3 0-64 28.7-64 64V320c0 35.3 28.7 64 64 64zM64 128c-35.3 0-64 28.7-64 64V448c0 35.3 28.7 64 64 64H256c35.3 0 64-28.7 64-64V416H272v32c0 8.8-7.2 16-16 16H64c-8.8 0-16-7.2-16-16V192c0-8.8 7.2-16 16-16H96V128H64z"
            />
          </svg>
        </div>
        <button class="button block lg mt-4" :disabled="loading" type="submit">
          Продолжить
        </button>
      </form>
    </div>
    <div class="row payment" v-if="paymentDetails">
      <div class="col-12 col-md-6">
        <div class="dashboard-personal-wrap">
          <div class="profile-info-column-one mt-0 refill">
            <div>
              <span>Минимальная сумма</span>
              <b>{{ paymentDetails.min_sum }}</b>
            </div>
          </div>
        </div>
      </div>
      <div class="col-12 col-md-6">
        <div class="dashboard-personal-wrap ellipse">
          <div class="profile-info-column-one mt-0 refill">
            <div>
              <span>Максимальная сумма</span>
              <b>{{ paymentDetails.max_sum }} {{ currency }}</b>
            </div>
          </div>
        </div>
      </div>
      <div class="col-12 col-md-6">
        <div class="dashboard-personal-wrap ellipse">
          <div class="profile-info-column-one mt-0 refill">
            <div>
              <span>Комиссия</span>
              <b>{{ paymentDetails.addination_commission }}%</b>
            </div>
          </div>
        </div>
      </div>
      <div class="col-12 col-md-6">
        <div class="dashboard-personal-wrap ellipse">
          <div
            v-if="createFiat.length > 0"
            class="profile-info-column-one mt-0 refill"
          >
            <div>
              <span>Курс</span>
              <b
                >1.00 {{ createFiat[0].currency_f }} = {{ createFiat[0].rate }}
                {{ createFiat[0].currency_t }}</b
              >
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-window hwifth" v-if="modalStatus">
      <form
        class="modal-content"
        @submit.prevent="confirmPayment"
        v-if="confirm"
        autocomplete="off"
      >
        <h1 class="modal-title text-center mb-3">Подтверждение платежа</h1>
        <div class="dashboard-table-row table-row-v2">
          <div class="dashboard-table-column">Сумма к списанию</div>
          <div class="dashboard-table-column">{{ confirm.credit }} USDT</div>
        </div>
        <div class="dashboard-table-row table-row-v2">
          <div class="dashboard-table-column">Сумма к зачислению</div>
          <div class="dashboard-table-column">{{ confirm.amount }} KGS</div>
        </div>
        <div class="dashboard-table-row table-row-v2">
          <div class="dashboard-table-column">Комиссия</div>
          <div class="dashboard-table-column">{{ paymentDetails.commission }} KGS</div>
        </div>
        <div class="dashboard-table-row table-row-v2">
          <div class="dashboard-table-column over">{{ confirm.description }}</div>
        </div>
        <template v-if="twofactor">
          <label class="form-label mt-3">Google 2Fa Authenticator</label>
          <div class="form-group">
            <input
              type="number"
              class="form-input"
              ref="code"
              placeholder="Введите защитный код"
              :disabled="loading"
              required
            />
          </div>
        </template>
        <button class="button block lg" type="submit" :disabled="loading">
          Подтвердить
        </button>
      </form>
    </div>
    <span class="tp">{{ firstCategoryTitle }}</span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      paymentId: null,
      credit: "",
      modalStatus: false,
      quantity: "",
      address: "",
      balance: "",
      network: "",
      amount: "",
      loading: false,
      twofactor: false,
      regex: "",
    };
  },
  watch: {
    "$route.params.id": {
      immediate: true,
      handler(newId) {
        this.paymentId = newId;
        this.fetchSinglePaymentDetail();
      },
    },
  },
  computed: {
    firstBalance() {
      const balances = this.$store.getters.getCurrencies;
      return balances.length > 0 ? balances[0] : null;
    },
    paymentDetails() {
      return this.$store.getters.paymentDetails;
    },
    createFiat() {
      return this.$store.getters.createFiat;
    },
    confirm() {
      return this.$store.getters.confirm;
    },
    firstCategoryTitle() {
      if (!this.paymentDetails || this.paymentDetails.length === 0) {
        return "";
      }
      return this.paymentDetails.category ? this.paymentDetails.category.title : "";
    },
  },
  methods: {
    setMaxAmount() {
      if (this.firstBalance) {
        this.amount = this.firstBalance.balance;
        this.calculate();
      }
    },
    async confirmPayment() {
      const data = {
        data: this.confirm.id,
      };
      if (this.twofactor) {
        data["2fa_otp"] = String(this.$refs.code.value);
      }
      await this.$store.dispatch("createPaymentConfirm", data);
      this.closeModal();
    },
    showModal() {
      this.$store.dispatch("twofactor", false);
      this.modalStatus = true;
    },
    closeModal() {
      this.modalStatus = false;
    },
    pasteFromClipboard() {
      navigator.clipboard
        .readText()
        .then((clipboardText) => {
          this.address = clipboardText;
        })
        .catch((error) => {
          console.error("Failed to read clipboard contents: ", error);
        });
    },
    async fetchSinglePaymentDetail() {
      try {
        const paymentDetails = await this.$store.dispatch("getPaymentDetails", this.paymentId);
        await this.$store.dispatch("getCreateFiat", this.paymentId);
        this.credit = paymentDetails.credit;
        this.balance = paymentDetails.balance;
      } catch (error) {
        console.error("Error fetching single payment detail:", error);
      }
    },
    calculate() {
      if (this.amount > 0 && this.createFiat.length > 0) {
        const rate = this.createFiat[0].rate;
        const quantity = this.amount * rate;
        const commission = (quantity * parseFloat(this.paymentDetails.commission)) / 100;
        const result = quantity - commission - parseFloat(this.paymentDetails.addination_commission);
        this.credit = this.numberFormat(result >= 0 ? result : 0, 2);
      } else {
        this.credit = 0;
      }
    },
    numberFormat(value, decimalPlaces) {
      return parseFloat(value).toFixed(decimalPlaces);
    },
    validateAddress() {
      const regexPattern = new RegExp(this.regex);
      return regexPattern.test(this.address);
    },
    async onsubmit() {
      if (this.amount < parseFloat(this.paymentDetails.min_qty)) {
        this.notification("Минимальная сумма " + this.paymentDetails.min_qty + " " + this.amount);
      } else {
        if (!this.address || !this.validateAddress()) {
          this.notification("Введите правильный реквизит");
        } else {
          const data = {
            serviceId: this.paymentId,
            amount: this.numberFormat(this.amount, 2),
            requisite: this.address,
          };

          try {
            const response = await this.$store.dispatch("createPayment", data);
            if (response && response.response) {
              this.confirm = this.$store.getters.confirm;
            } else if (response && response.messages) {
              this.notification(response.messages);
            }
            this.openConfirmationModal();
          } catch (error) {
            console.error("Error creating payment:", error);
            if (error.response && error.response.data && error.response.data.messages) {
              this.notification(error.response.data.messages);
            } else {
              this.notification("Ошибка при создании платежа");
            }
          }
        }
      }
    },
    openConfirmationModal() {
      this.modalStatus = true;
    },
  },
};
</script>
