<template>
  <div>
    <h1 class="dashboard-title justify-content-between">
      <span class="top">{{ firstCategoryTitle }}</span>
      <div>
        <input
          type="text"
          name="search"
          class="form-input-buy"
          v-model="searchValue"
          placeholder="Поиск"
          autocomplete="off"
        />
      </div>
    </h1>
    <div class="list-payment">
      <router-link
        :to="'/dashboard/payment-detail/' + detail.id"
        class="detail-payment-detail"
        v-for="detail in filteredPaymentDetails"
        :key="detail.name"
        :class="{ highlighted: detail.id === '15' }"
      >
          <img :src="detail.logo" v-if="detail.logo" />
          <img v-else src="./../../assets/img/payment-logo.svg" alt="" />
          <div>
            <p>{{ detail.name }}</p>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      paymentId: this.$route.params.id,
      searchValue: "",
    };
  },
  watch: {
    "$route.params.id": {
      immediate: true,
      handler(newId) {
        this.paymentId = newId;
        this.fetchSinglePaymentDetail();
      },
    },
  },
  computed: {
    singlePaymentDetail() {
      return this.$store.getters.singlePaymentDetail;
    },
    filteredPaymentDetails() {
      if (!this.searchValue.trim()) {
        return this.singlePaymentDetail;
      } else {
        const searchTerm = this.searchValue.trim().toLowerCase();
        return this.singlePaymentDetail.filter((detail) =>
          detail.name.toLowerCase().includes(searchTerm)
        );
      }
    },
    firstCategoryTitle() {
      if (!this.singlePaymentDetail || this.singlePaymentDetail.length === 0) {
        return "";
      }
      const uniqueCategories = [
        ...new Set(
          this.singlePaymentDetail.map((detail) => detail.category.title)
        ),
      ];
      return uniqueCategories.length > 0 ? uniqueCategories[0] : "";
    },
  },
  methods: {
    async fetchSinglePaymentDetail() {
      try {
        await this.$store.dispatch("getSinglePaymentDetail", this.paymentId);
      } catch (error) {
        console.error("Error fetching single payment detail:", error);
      }
    },
  },
};
</script>
