export default {
  state: {
    paymentDetail: [],
    singlePaymentDetail: [],
    paymentDetails:[],
    createFiat: [],
    confirm: {},
  },
  mutations: {
    setPaymentDetail(state, payload) {
      state.paymentDetail = payload.list;
    },
    setSinglePaymentDetail(state, payload) {
      state.singlePaymentDetail = payload.list;
    },
    setPaymentDetails(state, payload) {
      state.paymentDetails = payload.detail
    },
    setCreateFiat(state , payload){
      state.createFiat = payload.currencies
    },
    setCreatePayment(state, data) {
      state.confirm = data.confirm;
    },
  },
  actions: {
    async createPayment({dispatch, commit}, data) {
      dispatch('sendRequest', {method: 'POST', endpoint: '/payment-services/create', data})
      .then(response => response.json())
      .then(data => {
          if(data.response === true) {
              commit('setCreatePayment', data)
          } else {
              commit('notification', {message: data.messages})
          }
      })
  },
  async createPaymentConfirm({ dispatch, commit, state }, data) {
    try {
      const response = await dispatch('sendRequest', {
        method: 'POST',
        endpoint: `/payment-services/confirm/${state.confirm.id}`,
        data,
      });
      const result = await response.json();
      if (result.response === true) {
        commit('setCreatePaymentConfirm', result);
      } else {
        commit('notification', { message: result.messages });
      }
    } catch (error) {
      commit('notification', { message: 'Error confirming payment' });
    }
  },
    async getPaymentDetail({ dispatch, commit }, id = '') {
      try {
        const response = await dispatch('sendRequest', { method: 'GET', endpoint: `/payment-services/list${id}` });
        const data = await response.json();
        commit('setPaymentDetail', data);
      } catch (error) {
        console.error( error);
      }
    },
    async getCreateFiat({ dispatch, commit }, fiat) {
      try {
        const response = await dispatch('sendRequest', { method: 'GET', endpoint: `/currencies/fiat` , fiat });
        const data = await response.json();
        commit('setCreateFiat', data);
      } catch (error) {
        console.error( error);
      }
    },
    async getSinglePaymentDetail({ dispatch, commit }, id) {
      try {
        const response = await dispatch('sendRequest', { method: 'GET', endpoint: `/payment-services/list/${id}` });
        const data = await response.json();
        commit('setSinglePaymentDetail', data);
      } catch (error) {
        console.error( error);
      }
    },
    async getPaymentDetails({ dispatch, commit },id){
      try {
        const response = await dispatch('sendRequest', { method: 'GET', endpoint: `/payment-services/detail/${id}` });
        const data = await response.json();
        commit('setPaymentDetails', data);
      } catch (error) {
        console.error(error);
      }
    }},   
  getters: {
    paymentDetail(state) {
      return state.paymentDetail;
    },
    singlePaymentDetail(state) {
      return state.singlePaymentDetail;
    },
    paymentDetails(state){
      return state.paymentDetails;
    },
    createFiat(state){
      return state.createFiat;
    },
    confirm(state) {
      return state.confirm;
    }
  },
};
