export default {
    state: {
      contactList: '' 
    },
    mutations: {
      setContact(state, payload) {
        state.contactList = payload.detail.content; 
      }
    },
    actions: {
      async getContactList({dispatch, commit}) {
        dispatch('sendRequest', {method: 'GET', endpoint: '/pages/contacts'})
        .then(response => response.json())
        .then(data => {
            commit('setContact', data)
        })
    }
    },
    getters: {
      getContactList(state) {
        return state.contactList;
      }
    }
  };
  