<template>
  <section class="currencies">
    <div class="container">
      <h1>Сводка по криптовалютам на сегодня</h1>
      <div class="currencies-table currence-mobile borders">
        <div class="row currencies-row border-bottom none">
          <div class="col-6 col-md currencies-column-one m">Торговая пара</div>
          <div class="col-6 col-md currencies-column m">Цена</div>
          <div class="col-md currencies-column">Обьем 24ч (USDT)</div>
          <div class="col-md currencies-column">Рынок</div>
          <div class="col-md currencies-column">Действие</div>
        </div>
        <div class="currencies-row border-bottom curence-row-mobile">
          <div>
            <p>Торговая </p>
            <p>пара</p>
          </div>
          <div>
            <p>Цена/Обьем</p>
            <p> 24ч (USDT)</p>
          </div>
          <div>
            <p>Действие</p>
          </div>
        </div>
        <template
          v-for="(currency, index) in getMainData.currencies"
          :key="index"
        >
          <div
            class="row currencies-row border-bottom none"
            v-if="currency.rate"
          >
            <div class="col-6 col-md currencies-column-one currencies-column-md m lefs">
              <img :src="currency.logo" :alt="currency.name" />
              <span>{{ currency.name }}</span>
            </div>

            <div class="col-6 col-md currencies-column currencies-column-md m">
              {{ currency.rate }}
            </div>
            <div class="col-6 col-md currencies-column currencies-column-md">
              <span
                :class="
                  String(currency.difference).search('-') !== -1
                    ? 'text-danger'
                    : 'text-success'
                "
                >{{ currency.difference }}</span
              >
            </div>

            <div class="col-6 col-md currencies-column currencies-column-md">
              <img
                v-if="String(currency.difference).search('-') !== -1"
                src="../../assets/img/icons/app.icon.svg"
                alt=""
                class="difference-img left "
              />
              <img
                v-else
                src="./../../assets/img/icons/down.svg"
                class="differenc-img left"
                alt="difference-img"
              />
            </div>

            <div class="col-md currencies-column currencies-column-md">
              <router-link
                :to="getBuyLink(currency)"
                class="currencies-button-sale"
              >
                Покупка</router-link
              >
              <router-link
                :to="getSellLink(currency)"
                class="currencies-button"
                >Продажа</router-link
              >
            </div>
          </div>
        </template>
        <template
          v-for="(currency, index) in getMainData.currencies"
          :key="index"
        >
          <div class="curensies-row-none">
            <div class="currencies-column">
              <img :src="currency.logo" :alt="currency.name" />
              <span>{{ currency.currency }}</span>
            </div>
            <div>
              <p>
                {{ currency.rate }}
              </p>
              <div class="rates-none">
                <span
                  :class="
                    String(currency.difference).search('-') !== -1
                      ? 'text-danger'
                      : 'text-success'
                  "
                  >{{ currency.difference }}</span
                >
                <div>
                  <img
                    v-if="String(currency.difference).search('-') !== -1"
                    src="../../assets/img/icons/app.icon.svg"
                    alt=""
                    class="difference-img"
                  />
                  <img
                    v-else
                    src="./../../assets/img/icons/down.svg"
                    class="differenc-img"
                    alt="difference-img"
                  />
                </div>
              </div>
            </div>
            <div class="currencies-column-none">
              <router-link
                :to="getBuyLink(currency)"
                class="currencies-button-sale"
              >
                Покупка</router-link
              >
              <router-link
                :to="getSellLink(currency)"
                class="currencies-button"
                >Продажа</router-link
              >
            </div>
          </div>
        </template>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  computed: {
    getMainData() {
      return this.$store.getters.getMainData;
    },
  },
  methods: {
    hasBalance(currency) {
      const balance = this.$store.state.balances?.[currency.currency];
      return balance && balance > 0;
    },
    getBuyLink(currency) {
      return this.hasBalance(currency) ? `/dashboard/buy/${currency.currency}` : '/dashboard/buy';
    },
    getSellLink(currency) {
      return this.hasBalance(currency) ? `/dashboard/sell/${currency.currency}` : '/dashboard/sell';
    },
    rateFormat(data) {
      const rates = [];
      Object(data.rates).forEach((item, index) => {
        rates[index++] = item.price;
      });
      return [{ data: rates }];
    },
  },
};
</script>
